
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 80%;
  max-width: 100%;
  border: 1px solid black;
  margin: auto;

}


thead th {
  border-bottom: 2px solid #dedede; }

tfoot th {
  border-top: 2px solid #dedede; }

td {
  border-bottom: 1px solid #dedede; }

th,
td {
  text-align: left;
  padding: .5rem; }

caption {
  padding: 1rem 0;
  caption-side: bottom;
  color: #ababab; }

.striped-table tbody tr:nth-child(odd) {
  background-color: #f8f8f8; }

.contain-table {
  overflow-x: auto; }

@media (min-width: 600px) {
  .contain-table {
    width: 100%; } }

button {
  background-color: blue;
  color: #FFF;
  height: 30px;
  width: 100px;
  border-radius: 5px;
}